.newsletter {
  background-color: var(--clr-newsletter-back-darker);

  .container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: rem(100);
  }

  &__content {
    width: 100%;
    max-width: 55ch;
  }

  &__back {
    background-image: url("../img/newsletter/bg.jpg");
    background-image: image-set(url("../img/newsletter/bg.webp") 1x,
        url("../img/newsletter/bg@2x.webp") 2x,
        url("../img/newsletter/bg.jpg") 1x,
        url("../img/newsletter/bg@2x.jpg") 2x);
    background-repeat: no-repeat;
    background-position: top center;
    min-height: rem(360);

    @include media("md") {
      background-image: none;
      background-color: var(--clr-newsletter-back);
    }
  }

  &__heading {
    margin-top: 0;
    margin-bottom: rem(8);
    color: var(--clr-newsletter-text);

    font-family: var(--font-accent);
    font-weight: 500;
    font-size: rem(40);
    line-height: lh(44, 40);
    letter-spacing: -0.4px;

    @include media("sm") {
      font-size: rem(28);
      line-height: lh(34, 28);
      font-weight: 500;
      letter-spacing: -0.4px;
    }
  }

  &__subheading {
    font-size: rem(18);
    line-height: lh(30, 18);
    font-weight: 400;
    color: var(--clr-newsletter-text);
    margin-top: 0;
    margin-bottom: rem(32);

    @include media("sm") {
      font-size: 14;
      line-height: lh(22, 14);
      font-weight: 400;
    }
  }
}

.newsletter-form {
  color: var(--clr-newsletter-form-text);
  display: flex;
  align-items: center;
  padding: rem(12) 0;
  column-gap: rem(8);
  border-bottom: 1px solid var(--clr-newsletter-form-underline);
  
  &__label {
    height: rem(24);
  }

  &__icon {
    fill: var(--clr-newsletter-mail-icon);
  }

  &__input {
    all: unset;
    margin-right: auto;
    font-size: rem(16);
    width: 100%;
    height: rem(28);
  }

  &__button {
    border-bottom: none;
  }
}